}

/* style autofill input field */
input:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #fff !important; /* Цвет фона */
    -webkit-text-fill-color: #141414 !important; /* цвет текста */
    color: #141414 !important; /* цвет текста */
    border-radius: 16px; /* округление поля */
}

/* Apple Pay Button CSS: start */
.apple-pay-button {
    cursor: pointer;
    display: block;
    -webkit-appearance: -apple-pay-button;
    -apple-pay-button-type: buy;
    height: 44px;
    -apple-pay-button-style: black;
}

@media screen and (min-width: 736px) {
    .apple-pay-button {
        cursor: pointer;
        display: block;
        -webkit-appearance: -apple-pay-button;
        -apple-pay-button-type: buy;
        height: 56px;
        -apple-pay-button-style: black;
    }
}
/* Apple Pay Button CSS: end */

/* swipe animation : start */
.swipe_getout {
    animation-name: swipe_getout;
    animation-duration: 0.8s;
    animation-delay: 0.1s;
    animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
    animation-iteration-count: 1;
}

@-moz-keyframes swipe_getout {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-90vh);
    }
}

@-webkit-keyframes swipe_getout {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-90vh);
    }
}

@-o-keyframes swipe_getout {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-90vh);
    }
}

@keyframes swipe_getout {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-90vh);
    }
}

/* swipe animation : end */


