/*
css animation Iban business-connect start
 */
@keyframes arc-purple {
    from {
        stroke-dasharray: 900;
    }
    to {
        stroke-dasharray: 949;
    }
}
@keyframes arc-gray {
    from {
        stroke-dasharray: 1474;
    }
    to {
        stroke-dasharray: 1425;
    }
}

[id='text-timer'] {
    font-size: 80px;
    fill: url(#paint1_linear);
}
[id='circle-gray'],
[id='circle-purple'] {
    animation-duration: 2s;
    animation-iteration-count: 1;
    animation-timing-function: linear;
    fill: none;
    stroke-linecap: round;
    stroke-width: 6;
    transform-origin: 120px 112px;
}
[id='circle-gray'] {
    animation-fill-mode: forwards;
    stroke: #f2f2f5;
    stroke-dasharray: 1474;
    stroke-dashoffset: 910;
    transform: rotate(-92.5deg) scaleY(-1);
}
[id='circle-purple'] {
    animation-fill-mode: forwards;
    stroke: url(#paint0_linear);
    stroke-dasharray: 910;
    stroke-dashoffset: 910;
    transform: rotate(-87deg);
}
/*
css animation Iban business-connect end
 */
.animatedBanks {
    animation: dancingBanks 6s cubic-bezier(0.65, 0, 0.45, 1) infinite;
}
@-moz-keyframes dancingBanks {
    0% {
        opacity: 0;
    }
    7% {
        opacity: 0;
    }
    42% {
