a[class^='bg-icon-'] {
    cursor: pointer;
}

[class^='bg-icon-'] {
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: inline-block;
    position: relative;
    vertical-align: top;
}

[class^='bg-icon-16'] {
    height: 16px;
    min-width: 16px;
    width: 16px;
}

[class^='bg-icon-16'][class$='_info'] {
    background-image: url('data:image/svg+xml;base64,');
}

[class^='bg-icon-16'][class$='_info_gray'] {
    background-image: url('data:image/svg+xml;base64,');
}

[class^='bg-icon-16'][class$='_search_gray'] {
    background-image: url('data:image/svg+xml;base64,');
}

